

.summoner-object-component
.summoner-object-wrapper {
position: absolute;
top: 0;
bottom: 0;
left: 0;
right: 0;
letter-spacing: 0.8px;
font-size: 16px;
font-weight: 300;
text-shadow: 1px 1px 2px #000, 0px 1px 2px #000, 0px 0px 3px #000,
  0px 0px 3px #000, 1px 1px 4px #000;
}


.summoner-object-component
.summoner-object-wrapper
.background,

.summoner-object-component
.summoner-object-wrapper
.old-background {
position: absolute;
top: 0;
bottom: 0;
left: 0;
right: 0;
background-size: 241%;
background-position: 52% 28%;
opacity: 1;
}


.summoner-object-component
.summoner-object-wrapper
.background-wrapper {
position: absolute;
top: 0;
bottom: 0;
left: 0;
right: 0;
}


.summoner-object-component
.summoner-object-wrapper
.action-text {
direction: ltr;
opacity: 0;
position: absolute;
bottom: 20px;
transition: opacity 0.2s;
}


.summoner-object-component
.summoner-object-wrapper
.edge-border {
opacity: 0;
position: absolute;
top: 0;
width: 0;
height: 100%;
border-left: 0px solid #000;
border-right: 0px solid #000;
transition: width 0.15s;
}

.summoner-object-component
.summoner-object-wrapper
.spells {
opacity: 1;
transition: opacity 0.3s;
}

.summoner-object-component
.summoner-object-wrapper
.spell {
width: 30px;
border: 2px solid #000;
position: absolute;
box-shadow: 0 0 1px #000;
}

.summoner-object-component
.summoner-object-wrapper
.spell:first-child {
top: 7px;
}

.summoner-object-component
.summoner-object-wrapper
.spell:nth-child(2) {
top: 39px;
}

.summoner-object-component
.summoner-object-wrapper
.summoner-name {
position: absolute;
bottom: 2px;
transition: left 0.2s, right 0.2s;
}


.summoner-object-component.no-champion
.spells {
opacity: 0;
}


.summoner-object-component.is-picking-now
.action-text {
opacity: 1;
}


.summoner-object-component.champion-not-locked
.background,

.summoner-object-component.champion-not-locked
.old-background {
-webkit-filter: brightness(0.8);
}


