#blueParty
.summoner-object-component
.summoner-object-wrapper
.spell {
left: 11px;
}


#blueParty
.summoner-object-component
.summoner-object-wrapper
.summoner-name {
right: 9px;
}


#blueParty
.summoner-object-component
.summoner-object-wrapper
.edge-border {
right: 0px;
background: var(--left-side-color);
}

#blueParty .summoner-object-component{
  color: var(--left-side-text-color);
}

#redParty .summoner-object-component{
  color: var(--right-side-text-color);
}


#blueParty
.summoner-object-component
.summoner-object-wrapper
.action-text {
right: 15px;
}


#blueParty
.summoner-object-component.is-picking-now
.edge-border {
width: 4px;
border-left: 3px solid #000;
opacity: 1;
}


#blueParty
.summoner-object-component.is-picking-now
.summoner-name {
right: 15px;
}


#blueParty
.summoner-object-component.is-picking-now
.action-text {
animation: fadeInRightAnimation 0.2s,
  flickerAnimation 3s 0.2s cubic-bezier(0.21, 0.89, 0.2, 0.88) infinite;
}


#blueParty
.summoner-object-component
.background-wrapper {
transform: scaleX(-1);
}


#redParty
.summoner-object-component
.summoner-object-wrapper
.spell {
right: 11px;
}


#redParty
.summoner-object-component
.summoner-object-wrapper
.summoner-name {
left: 10px;
}


#redParty
.summoner-object-component
.summoner-object-wrapper
.edge-border {
left: 0px;
background: var(--right-side-color);
}


#redParty
.summoner-object-component
.summoner-object-wrapper
.action-text {
left: 15px;
}


#redParty
.summoner-object-component.is-picking-now
.edge-border {
width: 4px;
border-right: 3px solid #000;
opacity: 1;
}


#redParty
.summoner-object-component.is-picking-now
.summoner-name {
left: 15px;
}


#redParty
.summoner-object-component.is-picking-now
.action-text {
animation: fadeInLeftAnimation 0.2s,
  flickerAnimation 3s 0.2s cubic-bezier(0.21, 0.89, 0.2, 0.88) infinite;
}


.party .summoner-wrapper {
  width: 100%;
  height: 80px;
  position: relative;
  margin-bottom: 4px;
}

 .party .summoner-wrapper:nth-child(3) {
  margin-bottom: 23px;
}


  .party
  .summoner-wrapper
  .base-color-background {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  -webkit-filter: saturate(0.6);
}


  .party
  .summoner-wrapper
  .gradient-background-overlay {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-image: linear-gradient(
    to right,
    rgba(0, 0, 0, 0.83) 0%,
    rgba(0, 0, 0, 0.72) 50%,
    rgba(0, 0, 0, 0.83) 100%
  );
}


  .party
  .summoner-wrapper:first-child
  .gradient-background-overlay {
  background-image: radial-gradient(
    ellipse 82% 128% at 50% 90%,
    rgba(0, 0, 0, 0.72) 0%,
    rgba(0, 0, 0, 0.91) 100%
  );
}


  .party
  .summoner-wrapper:last-child
  .gradient-background-overlay {
  background-image: radial-gradient(
    ellipse 82% 128% at 50% 10%,
    rgba(0, 0, 0, 0.72) 0%,
    rgba(0, 0, 0, 0.91) 100%
  );
}


  #blueParty
  .summoner-wrapper
  .base-color-background {
  background: var(--left-side-color);
}


  #redParty
  .summoner-wrapper
  .base-color-background {
  background: var(--right-side-color);
  }

